@import url(https://fonts.googleapis.com/css?family=Raleway:400,700,900,400italic,700italic,900italic);
.App {
  text-align: center;
}
body {
  /*
  background: rgba(0, 0, 0, 0.7);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(3.5px);
  -webkit-backdrop-filter: blur(3.5px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.18);
  */
  background: linear-gradient(90deg, #e3ffe7 0%, #d9e7ff 100%);
}
h1 {
  font-family: "Roboto Mono", monospace;
  font-size: 100px;
  color: cyan;
}

.card:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.12), 0 4px 8px rgba(0, 0, 0, 0.06);
}
.card {
  border-radius: 4px;

  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.08), 0 0 6px rgba(0, 0, 0, 0.05);
  transition: 0.3s transform cubic-bezier(0.155, 1.105, 0.295, 1.12),
    0.3s box-shadow,
    0.3s -webkit-transform cubic-bezier(0.155, 1.105, 0.295, 1.12);

  cursor: pointer;
}
.card-img-top {
  width: 285px;
  height: 285px;
}
.cardbackground {
  background: rgba(74, 144, 226, 0.85);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.18);
}

.container-fluid {
  background: rgba(74, 144, 226, 0.85);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.18);
  height: 500px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

*,
:before,
:after {
  box-sizing: border-box;
}

h1 {
  -webkit-animation: text-shadow 1.5s ease-in-out infinite;
          animation: text-shadow 1.5s ease-in-out infinite;

  font-weight: 900;
  line-height: 1;
}

h1:hover {
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
}

a {
  color: #024794;
}

a:hover {
  text-decoration: none;
}

@-webkit-keyframes text-shadow {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    text-shadow: 0 0 0 #0c2ffb, 0 0 0 #2cfcfd, 0 0 0 black, 0 0 0 #fefc4b;
  }

  20% {
    -webkit-transform: translateY(-1em);
            transform: translateY(-1em);
    text-shadow: 0 0.125em 0 #0c2ffb, 0 0.25em 0 #2cfcfd, 0 -0.125em 0 black,
      0 -0.25em 0 #fefc4b;
  }

  40% {
    -webkit-transform: translateY(0.5em);
            transform: translateY(0.5em);
    text-shadow: 0 -0.0625em 0 #0c2ffb, 0 -0.125em 0 #2cfcfd, 0 0.0625em 0 black,
      0 0.125em 0 #fefc4b;
  }

  60% {
    -webkit-transform: translateY(-0.25em);
            transform: translateY(-0.25em);
    text-shadow: 0 0.03125em 0 #0c2ffb, 0 0.0625em 0 #2cfcfd,
      0 -0.03125em 0 black, 0 -0.0625em 0 #fefc4b;
  }

  80% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    text-shadow: 0 0 0 #0c2ffb, 0 0 0 #2cfcfd, 0 0 0 black, 0 0 0 #fefc4b;
  }
}

@keyframes text-shadow {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    text-shadow: 0 0 0 #0c2ffb, 0 0 0 #2cfcfd, 0 0 0 black, 0 0 0 #fefc4b;
  }

  20% {
    -webkit-transform: translateY(-1em);
            transform: translateY(-1em);
    text-shadow: 0 0.125em 0 #0c2ffb, 0 0.25em 0 #2cfcfd, 0 -0.125em 0 black,
      0 -0.25em 0 #fefc4b;
  }

  40% {
    -webkit-transform: translateY(0.5em);
            transform: translateY(0.5em);
    text-shadow: 0 -0.0625em 0 #0c2ffb, 0 -0.125em 0 #2cfcfd, 0 0.0625em 0 black,
      0 0.125em 0 #fefc4b;
  }

  60% {
    -webkit-transform: translateY(-0.25em);
            transform: translateY(-0.25em);
    text-shadow: 0 0.03125em 0 #0c2ffb, 0 0.0625em 0 #2cfcfd,
      0 -0.03125em 0 black, 0 -0.0625em 0 #fefc4b;
  }

  80% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    text-shadow: 0 0 0 #0c2ffb, 0 0 0 #2cfcfd, 0 0 0 black, 0 0 0 #fefc4b;
  }
}

@media (prefers-reduced-motion: reduce) {
  * {
    -webkit-animation: none !important;
            animation: none !important;
    transition: none !important;
  }
}

